.search {
  &-top {
    &-btn {
	  display: flex;
	  justify-content: space-between;
	  align-items: center;
	  padding-left: 20px;
	}
	&-arrow {
	  width: calc-percent(261,306);
	  margin-bottom: 10px;
	  padding-left: 20px;
	  display: flex;
	  justify-content: space-between;
	  @extend %tb-flex-between;
	  & > li {
	    width: calc-percent(125,261);
	    height: 35px;
	    border: 1px solid $colorBlue;
	    border-radius: 5px;
	    @include respond-to(480) {height: 27px;}
	    a {		
	      display: flex;
	      width: 100%;
	      height: 100%;
	      font-size: 18px;
	      color: #000;
	      align-items: center;
	      justify-content: center;
	      &:active {
	        color: $colorWhite;
	        background-color: $colorBlue;
	      }
	      @include respond-to(480) {font-size: 15px;}
	    }
	  }
    }
    &-title {
      @include label-green;
      justify-content: space-between;
      margin-top: 5px;	
    }
  }
  &-tb {
    width: calc-percent(280, 306);
    display: flex;
    margin-top: 60px;
    margin-left: 20px;
    justify-content: space-between;
    @extend %tb-flex-between;
    tbody {width: 100%;}
    tr {
      display: flex;
      width: 100%;
      &:nth-of-type(2) {margin: 6px 0;}
    }
    td:first-child {
      display: flex;
      width: calc-percent(80, 276);
      justify-content: flex-start;
      align-items: center;
      label {@include label-green;}
      @include respond-to(364) {
	    width: calc-percent(95, 276);
	  }
    }
    td:last-child {
      width: 100%;
      input {
        width: 99%;
      	max-height: 25px;
        height: 5.3vw; 
      	border: 1px solid $colorGrey;
      	border-radius: 5px;
      	background-color: $colorYellow;
      }
    }
    button {width: calc-percent(49, 205);}
    .btn-grey1 {
	  max-height: 35px;
	  height: 5.4vw;
	  min-height: 25px;
	  color: #ffffff;
	  background-color: #7f7f7f;
	  border: none;
	}
    &-list-btn {
      width: 100%;
      display: flex;
      justify-content: space-between;
        li {
          width: calc-percent(49, 205);
          a {
            display: flex;
		    width: 100%;
		    height: 100%;
		    font-size: 18px;
		    color: #000;
		    align-items: center;
		    justify-content: center;
		    @include respond-to(480) {
		      font-size: 14px;
		    }        		
          }
        }
    }
  }
}
